import {
  SET_EPOCH_SIZE,
  SET_CURRENT_TIME,
  SOFT_SET_CURRENT_TIME,
  SHIFT_CURRENT_TIME,
} from '../actions';

const initialState = {
  epochSize: 10,
  currentTime: 0, // Always seconds
};

const navigation = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SET_EPOCH_SIZE:
      return {
        ...state,
        epochSize: payload,
      };
    case SET_CURRENT_TIME:
      return {
        ...state,
        currentTime: payload,
      };
    case SOFT_SET_CURRENT_TIME:
      return payload < state.currentTime ||
        payload >= state.currentTime + state.epochSize
        ? {
            ...state,
            currentTime: payload,
          }
        : state;
    case SHIFT_CURRENT_TIME:
      return {
        ...state,
        currentTime: state.currentTime + payload,
      };
    default:
      return state;
  }
};

export default navigation;
